<template>
  <BaseLayout title="充值中心" bg="linear-gradient(180deg, #EEE, #F9F9F9 10%, #FFF 50%, #FFF)">
    <template #extra>
      <van-button icon="todo-list-o" :style="{width:'110px', borderRadius:'10px 0 0 10px', color: '#000'}" color="#F6F6F6" @click="$router.push('/recharge/record')">充值记录</van-button>
    </template>

    <div class="recharge">
      <van-sidebar class="sidebar" v-model="state.active" @change="onChange">
        <van-sidebar-item v-for="(cat,index) in state.cats" :key="index" :title="cat.name" v-show="cat.id > 0 || uid == 1" />
        <div class="recommend" @click="$router.push('/recharge/group')"><van-badge dot>批量充话费</van-badge></div>
        <div class="recommend" @click="$router.push('/recharge/mobile')" v-if="uid == 10000"><van-badge content="test">话费快充</van-badge></div>
      </van-sidebar>
      <div class="panel">
        <van-empty v-show="state.goods.length == 0" description="暂无项目"></van-empty>
        <van-cell class="goods" v-for="(goods,index) in state.goods" :key="index" :title="goods.title" :class="{active: state.id == goods.id}" @click="onChoose(goods)">
          <template #icon><van-icon :name="'//cdn.fuchijihua.com/assets/img/digital/' + goods.brand + '.png'" class="icon"></van-icon></template>
          <template #label><div class="face">￥{{goods.face_value}}</div><div class="price">售价：￥{{goods.price}}</div></template>
        </van-cell>
      </div>
    </div>

    <van-action-sheet v-model:show="state.id" :title="state.title">
      <div class="form">
        <van-field class="account" type="text" v-model="state.account" placeholder="充值账号"></van-field>
        <van-button class="submit" type="danger" block :disabled="!valid" @click="onRecharge">立即充值</van-button>
      </div>
      <div class="intro">{{state.intro}}</div>
    </van-action-sheet>

  </BaseLayout>
</template>

<style scoped>
.recharge {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  min-height: 500px;
}
.sidebar {
  width: 25%;
  height: 100%;
  background: #f7f8fa;
}
.panel {
  width: 75%;
  height: 100%;
  padding: 10px;
  background: #FFF;
}
.goods {
  margin-bottom: 10px;
  width: 100%;
  background: #FFF;
  border:solid 1px #CCC;
  border-radius: 5px;
  overflow: hidden;
}
.goods.active {
  background: #FEE;
  border-color: #F00;
}
.icon {
  position: relative;
  top: 2px;
  left: -10px;
  font-size: 42px;
}
.face {
  float: left;
  color: #999;
  font-size: 16px;
  font-weight: 800;
}
.price {
  float:right;
  font-size: 12px;
  color: #F00;
}

.form {
  margin: 20px 10px;
}
.account {
  border: solid 1px #F66;
  border-radius: 2px;
}
.submit {
  margin: 10px 0;
}
.intro {
  max-height: 100px;
  margin: 10px;
  background: #F6F6F6;
  color: #333;
  font-size: 12px;
  line-height: 20px;
  overflow: scroll;
}

.recommend {
  height: 40px;
  padding: 15px 0 0 12px;
  color: #F99;
  font-size: 14px;
}
</style>

<script>
import {reactive} from 'vue'
import {mapState, mapGetters} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  data() {
    const state = reactive({
      record: false,

      cats: [],
      goods: [],
      title: "",
      intro: "",
      id: 0,
      account: "",
    });
    return {state};
  },

  created() {
    this.$http.get("/digital/category").then((data) => {
      this.state.cats = data;
      this.onChange(0);
    });
  },

  computed: {
    valid() {
      return this.state.id > 0 && this.state.account.length > 0;
    },
    ...mapGetters(["uid"]),
  },

  methods: {
    onChange(index) {
      this.state.id = 0;
      this.state.intro = "";
      this.$http.post("/digital/list?cid=" + this.state.cats[index].id).then((data) => {
        this.state.goods = data;
      });
    },
    onChoose(goods) {
      this.state.id = goods.id;
      this.state.title = goods.title;
      this.state.intro = goods.intro;
    },
    onRecharge(){
      this.$http.post("/digital/recharge?id=" + this.state.id + "&account=" + this.state.account).then((data) => {
        this.state.id = 0;
        this.$dialog.alert({
          title: "订单已生成",
          message: "请点击右上角“充值记录”查看到账情况",
          theme: "round-button",
        });
      });
    },
  }
}
</script>
